					<img src="@/assets/jianto.png" alt="" />
				</div>
				<div class="box-m-r">应用更新时间：2020.12.14</div>
			</div>
			<div class="box-b">
				<div class="box-b-l">
					<p>App隐私政策：</p>
					<samp>查看隐私</samp>
					<img src="@/assets/jianto.png" alt="" />
				</div>
				<div class="box-b-r">开发者：杭州卷瓜网络有限公司</div>
			</div>
		</div>
		<nav class="chengnuv">
			<div class="chengnuv-l" @click="$router.push({ path: '/storelist' })"></div>
			<div class="chengnuv-m" @click="$router.push({ path: '/search' })">
				<img src="@/assets/nuv3.png" alt="" />
				<input type="text" pattern="学院风jk套装" />
			</div>
			<div class="chengnuv-r"></div>
		</nav>
		<section class="chengsection">
			<ul class="chengsectionnav">
				<HomeSction v-for="n in chengnuv" :key="n._system_record_entry_id" :n="n" />
			</ul>
			<!-- this.addsearchvalue(n.title) -->
		</section>
		<section class="chengproductlist" v-if="chengproductlist">
			<ul>
				<van-list
					v-model="loading"
					:finished="finished"
					finished-text="没有更多了"
					@load="onLoad">
					<HomeProductList
						v-for="(n, i) in chengproductlist"
						:key="n.id"
						:n="n"
						:o="i % 2 === 0"
						:i="i"
						:c="1" />
				</van-list>
			</ul>
			<ul>
				<HomeProductList
					v-for="(n, i) in chengproductlist"
					:key="n.id"
					:n="n"
					:o="i % 2 === 1"
					:i="i"
					:c="2" />
			</ul>
		</section>
	</div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import HomeSction from "@/components/HomeSction.vue";
import HomeProductList from "@/components/HomeProductList.vue";

export default {
	name: "HomeView",
	data() {
		return {
			loading: false,
			finished: false,
			pages: 1,
		};
	},

	components: {
		HomeSction,
		HomeProductList,
	},

	computed: {
		...mapState(["chengnuv", "chengproductlist"]),
	},
	methods: {
		...mapMutations([
			"addchengnuv",
			"addchengproductlist",
			"pushchengproductlist",
		]),

		onLoad() {
			console.log("触底了");
			this.pages++;
			this.axios
				.get("https://apis.netstart.cn/mogujie/video/item", {
					params: {
						page: this.pages,
					},
				})
				.then((ren) => {
					console.log(ren.data.data.list);
					this.pushchengproductlist(ren?.data?.data?.list);
					this.loading = false;
					if (this.pages >= 3) {
						this.finished = true;
					}
				});
		},
	},
	created() {
		//首页nuv
		this.axios
			.get("https://apis.netstart.cn/mogujie/homeNav.json")
			.then((ren) => {
				this.addchengnuv(ren?.data?.slice(0, 10));
			});

		//首页商品列表
		this.axios
			.get("https://apis.netstart.cn/mogujie/video/item", {
				params: {
					page: this.pages,
				},
			})
			.then((ren) => {
				// console.log(ren.data.data.list);
				this.addchengproductlist(ren?.data?.data?.list);
			});
	},
};
</script>
