<template>
	<li
		class="HomeSction"
		@click="
			n.title !== '新人福利'
				? (addsearchvalue(n.title),
				$router.push({ path: '/serp', query: { q: n.title } }))
				: ''
		">
		<img :src="n.image + '_640x640.v1cAC.40.webp'" alt="" />
		<p class="HomeSctionp">{{ n.title }}</p>
	</li>
</template>

<script>
import { mapMutations } from "vuex";

export default {
	name: "HomeSction",
	props: {
		n: Object,
	},
	data() {
		return {};
	},

	mounted() {},

	methods: {
		...mapMutations(["addsearchvalue"]),
	},
};
</script>

<style lang="less" scoped>
.HomeSction {
	width: 20%;
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	margin: 5rem 0;
	img {
		width: 50rem;
		height: 50rem;
	}
	.HomeSctionp {
		width: 100%;
		text-align: center;
	}
}
</style>
