import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    //home
    chengnuv: null,
    chengproductlist: null,

    //store
    chengstore: null,
    chengyoulove: null,

    //storelist
    storelist: null,
    storelistclassify: null,
    //storelist 综合
    storelistsynthesize: null,
    //storelist 销量
    storelistsales:null,
    //storelist 新品
    storelistnew:null,

    //search 搜索
    searchvalue:"",
    hotsearch: null,
    historysearch:JSON.parse(window.localStorage.getItem("historysearch")) || [],

    // SERP 搜索结果
    // 综合
    SERPpop:null,
    // 销量
    SERPsell:null,
    // 新品
    SERPnew: null,
    
    //commodity商品
    commodity: null,
    
    //cart 购物车
    cart:JSON.parse(window.localStorage.getItem("cart")) || [],
  },
  getters: {
    
  },
  mutations: {
    addchengnuv(state,n) {
      if (n) {
        state.chengnuv = n;
      }
    },
    addchengproductlist(state,n) {
      if (n) {
        state.chengproductlist = n;
      }
    },
    pushchengproductlist(state, n) {
      if (n) {
        state.chengproductlist.push(...n);
      }
    },

    addchengstore(state,n) {
      if (n) {
        state.chengstore = n;
      }
    },
    addchengyoulove(state,n) {
      if (n) {
        state.chengyoulove = n;
      }
    },
    pushchengyoulove(state, n) {
      if (n) {
        state.chengyoulove.push(...n);
      }
    },

    addstorelist(state, n) {
      if (n) {
        state.storelist = n;
      }
    },
    addstorelistclassify(state, n) {
      if (n) {
        state.storelistclassify = n;
      }
    },


    addstorelistsynthesize(state, n) {
      if (n) {
        state.storelistsynthesize = n;
      }
    },
    addstorelistsales(state, n) {
      if (n) {
        state.storelistsales = n;
      }
    },
    addstorelistnew(state, n) {
      if (n) {
        state.storelistnew = n;
      }
    },

    addsearchvalue(state, n) {
      if (n) {
        state.searchvalue = n;
      }
      else {
        state.searchvalue = "时尚夏装";
      }
    },
    addhistorysearch(state, n) {
      if (n) {
        state.historysearch.push(n);
        state.historysearch=[...new Set(state.historysearch)]
        localStorage.setItem("historysearch", JSON.stringify(state.historysearch))
      }
    },
    deletehistorysearch(state) {
      state.historysearch = [];
      localStorage.setItem("historysearch", JSON.stringify([]))
    },
    addhotsearch(state, n) {
      if (n) {
        state.hotsearch = n;
      } 
    },
    // 搜索结果
    addSERPpop(state, n) {
      if (n) {
        state.SERPpop = n;
      }
    },
    addSERPsell(state, n) {
      if (n) {
        state.SERPsell = n;
      }
    },
    addSERPnew(state, n) {
      if (n) {
        state.SERPnew = n;
      }
    },

    //商品详情
    addcommodity(state, n) {
      if (n) {
        state.commodity = n;
      }
    },

    //添加购物车
    addcart(state, n) {
      if (n) {
        state.cart = [...state.cart, n];
      }
    },
    // 删除所有购物车信息
    deletecart(state) {
      state.cart = [];
    },
    //app保存购物车
    chengecart(state) {
      localStorage.setItem("cart", JSON.stringify(state.cart))
    },

    deletecartjudge(state, n) {
      state.cart.map((o) => o.id == n).id = false;
    }


  },
  actions: {
  },
  modules: {
  }
})
