<template>
	<!-- 订单列表页面 -->
	<div class="list">
		<nav class="list-nav">
			<div class="list-l" @click="back"><van-icon name="arrow-left" /></div>
			<div class="list-name">订单列表</div>
			<div class="list-r" @click="$router.push({ path: '/shoppingtrolley' })">
				<van-icon name="shopping-cart-o" />
			</div>
		</nav>
		<van-tabs v-model="activeName" animated swipeable>
			<van-tab title="代付款" name="b">
				<img src="@/assets/mx2.png" alt="" />
			</van-tab>
			<van-tab title="待发货" name="a">
				<section class="orderlist">
					<ul v-if="orderlist">
						<li v-for="(n, i) in orderlist" :key="i">
							<div
								class="orderlist-l"
								:style="{
									backgroundImage: 'url(' + n.img + '_400x534.v1cAC.40.webp)',
								}">
								<!-- <img :src="n.img + '_400x534.v1cAC.40.webp'" alt="" /> -->
							</div>
							<div class="orderlist-r">
								<div class="orderlist-r-l">
									<h3>{{ n.name }}</h3>
									<h5>
										颜色: <samp> {{ n.colo }}; </samp> 尺码: <samp> {{ n.size }}; </samp>
									</h5>
								</div>
								<div class="orderlist-r-r">
									<h3>¥{{ n.price }}</h3>
									<p>x{{ n.chengquantity }}</p>
								</div>
							</div>
						</li>
					</ul>
					<div class="zxc" v-else>
						<img src="@/assets/mx2.png" alt="" />
					</div>
				</section>
			</van-tab>
			<van-tab title="待收货" name="b">
				<img src="@/assets/mx2.png" alt="" />
			</van-tab>
			<van-tab title="待评价" name="c">
				<img src="@/assets/mx2.png" alt="" />
			</van-tab>
		</van-tabs>
	</div>
</template>

<script>
import { mapState } from "vuex";
export default {
	name: "ListView",

	data() {
		return {
			activeName: "a",
			orderlist: null,
		};
	},
	computed: {
		...mapState(["cart"]),
	},
	mounted() {},

	methods: {
		back() {
			if (window.history.length <= 1) {
				this.$router.push({ path: "/" });
				return false;
			} else {
				this.$router.go(-1);
			}
		},
	},
	created() {
		this.orderlist = this.cart.filter((o) => {
			return o.judge === true;
		});
	},
};
</script>

<style lang="less" scoped>
