<template>
	<!-- 订单页面  -->
	<div class="order">
		<nav class="ordernav">
			<div class="ordernav-l" @click="back()"><van-icon name="arrow-left" /></div>
			<div class="ordernav-m">确认订单</div>
			<div class="ordernav-r" @click="$router.push({ path: '/shoppingtrolley' })">
				<van-icon name="shopping-cart-o" />
			</div>
		</nav>
		<section class="ordertis">
			<p>温馨提示: 下载蘑菇街APP,及时跟进订单信息哟!</p>
		</section>
		<section class="orderdiz" @click="showPopup">
			<div class="orderdiz-l">
				<van-icon name="location-o" /><samp>请填写收货地址</samp>
			</div>
			<div class="orderdiz-r"><van-icon name="arrow" /></div>
		</section>
		<van-popup v-model="show" position="bottom">
			<div class="box">
				<div class="box-t">
					<img src="@/assets/mx.png" alt="" />
					<p>您还没有收货地址哦～</p>
				</div>
				<div class="box-b">+ 新增地址</div>
			</div>
		</van-popup>
		<section class="orderlist">
			<ul v-if="orderlist">
				<li v-for="(n, i) in orderlist" :key="i">
					<div
						class="orderlist-l"
						:style="{ backgroundImage: 'url(' + n.img + '_400x534.v1cAC.40.webp)' }">
						<!-- <img :src="n.img + '_400x534.v1cAC.40.webp'" alt="" /> -->
					</div>
					<div class="orderlist-r">
						<div class="orderlist-r-l">
							<h3>{{ n.name }}</h3>
							<h5>
								颜色: <samp> {{ n.colo }}; </samp> 尺码: <samp> {{ n.size }}; </samp>
							</h5>
						</div>
						<div class="orderlist-r-r">
							<h3>¥{{ n.price }}</h3>
							<p>x{{ n.chengquantity }}</p>
						</div>
					</div>
				</li>
			</ul>
		</section>

		<footer class="orderbot">
			<div class="orderbot-l">
				<div class="orderbot-l-l">总价:</div>
				<div class="orderbot-l-r">
					¥<samp>{{ chengjg }}</samp>
				</div>
			</div>
			<div class="orderbot-r">
				<div class="orderbot-r-l">共{{ orderg }}件商品</div>
				<div class="orderbot-r-r" @click="$router.push({ path: '/list' })">提交订单</div>
			</div>
		</footer>
	</div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
export default {
	name: "OrderView",

	data() {
		return {
			orderlist: null,
			orderg: 0,
			show: false,
		};
	},
	computed: {
		...mapState(["cart"]),
		chengjg: function () {
			let s = 0;
			this.cart
				.map(function (o) {
					if (o.judge === true) {
						return o.price;
					} else {
						return 0;
					}
				})
				.forEach((o) => (s += Number(o)));
			return s;
		},
	},

	mounted() {},

	methods: {
		...mapMutations(["addcart"]),
		back() {
			if (window.history.length <= 1) {
				this.$router.push({ path: "/" });
				return false;
			} else {
				this.$router.go(-1);
			}
		},
		showPopup() {
			this.show = true;
		},
	},
	created() {
		this.orderlist = this.cart.filter((o) => {
			return o.judge === true;
		});
		this.orderg = this.cart.filter((o) => {
			return o.judge === true;
		}).length;
	},
};
</script>

