<template>
	<!-- 首页视频界面 -->
	<div class="VideoView" v-if="chengproductlist">
		<!-- 视频 -->
		<video
			ref="video"
			@play="playing = true"
			@pause="playing = false"
			@durationchange="duration = $event.target.duration"
			@timeupdate="currentTime = $event.target.currentTime"
			:src="this.$route.query.videoUrl"></video>
		<!-- 开始阴影 -->
		<div class="videoyy" v-if="videoyy">
			<img
				src="@/assets/home1.png"
				alt=""
				@click="(videoyy = !videoyy), $refs.video.play()" />
		</div>
		<!-- 进度条 -->
		<div class="videojdt" v-else>
			<div class="videojdt-l" @click="(videoyy = !videoyy), $refs.video.pause()">
				<van-icon name="play" />
			</div>
			<div class="videojdt-m">
				<div class="videojdt-mt1">
					{{ Math.floor(currentTime / 60) }}分{{ Math.floor(currentTime % 60) }}秒
				</div>
				<input
					type="range"
					min="0"
					:max="duration"
					:value="tmpcurrentTime"
					name="videojdt"
					@change="
						draging = false;
						$refs.video.currentTime = $event.target.value;
					"
					id="" />
				<div class="videojdt-ma">
					<div
						class="videojdt-mb"
						:style="{ width: (200 * currentTime) / duration + 'rem' }"></div>
				</div>
				<div class="videojdt-mt2">
					{{ Math.floor(duration / 60) }}分{{ Math.floor(duration % 60) }}秒
				</div>
			</div>
		</div>
		<!-- 返回按键 -->
		<div class="videofan" @click="back()">
			<van-icon name="arrow-left" />
		</div>
		<ul class="video-r">
			<li>
				<div class="imgs">
					<van-image
						width="48rem"
						round
						height="48rem"
						fit="cover"
						:src="videoxq?.itemExplainList[0]?.actorInfo?.avatar" />
				</div>
				<h5>关注</h5>
			</li>
			<li>
				<van-icon name="star" />
				<h6>收藏</h6>
			</li>
			<li>
				<van-icon name="chat" />
				<h6>112</h6>
			</li>
			<li>
				<van-icon name="cart" />
				<h6>购物车</h6>
			</li>
		</ul>
		<div class="video-name" v-if="videoxq">
			<h2>@{{ videoxq?.itemExplainList[0]?.actorInfo?.name }}</h2>
			<h3>
				{{ videoxq?.itemExplainList[0]?.actorInfo?.height }}cm
				{{ videoxq?.itemExplainList[0]?.actorInfo?.weight }}kg <samp>|</samp>
				{{ videoxq?.itemExplainList[0]?.actorInfo?.fansCount }}
			</h3>
		</div>
		<div
			class="video-sp"
			@click="
				$router.push({
					path: '/commodity',
					query: {
						id: $route.query.itemId,
					},
				})
			">
			<div class="video-sp-l">
				<van-image
					width="48rem"
					height="48rem"
					fit="cover"
					:src="videoxq?.itemExplainList[0]?.itemInfo?.cover" />
			</div>
			<div class="video-sp-m">
				<h5>{{ videoxq?.itemExplainList[0]?.itemInfo?.title }}</h5>
				<p>
					<samp>待秒杀</samp> ¥{{
						videoxq?.itemExplainList[0]?.itemInfo?.discountPrice
					}}
				</p>
			</div>
			<div class="video-sp-r">立即购买</div>
		</div>
	</div>
</template>

<script>
import { mapState } from "vuex";
export default {
	name: "VideoView",

	computed: {
		...mapState(["chengproductlist"]),
	},
	data() {
		return {
			videoyy: true,

			playing: false,
			duration: 0,
			currentTime: 0,

			draging: false,
			tmpcurrentTime: this.currentTime,

			videoxq: null,
		};
	},

	mounted() {},

	methods: {
		back() {
			if (window.history.length <= 1) {
				this.$router.push({ path: "/" });
				return false;
			} else {
				this.$router.go(-1);
			}
		},
	},
	watch: {
		currentTime: function (n) {
			if (!this.draging) {
				this.tmpcurrentTime = n;
			}
		},
	},

	created() {
		console.log(this.$route.query.videoUrl);
		console.log(this.$route.query.itemId);
		console.log(this.$route.query.actorId);

		this.axios
			.get("https://apis.netstart.cn/mogujie/video/detail", {
				params: {
					itemId: this.$route.query.itemId,
					actorId: this.$route.query.actorId,
				},
			})
			.then((rem) => {
				console.log(rem.data.data);
				this.videoxq = rem.data.data;
			});
	},
};
</script>

<style lang="less" scoped>
.VideoView {
	width: 100%;
	height: 100vh;
	overflow: hidden;
	position: relative;
	z-index: 0;
	video {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
	}
	.videoyy {
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.2);
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;
		display: flex;
		justify-content: center;
		align-items: center;
		img {
			width: 55rem;
			height: 55rem;
		}
	}
	.videojdt {
		height: 50rem;
		width: 100%;
		position: absolute;
		left: 0;
		bottom: 0;
		z-index: 3;
		display: flex;
		// justify-content: center;
		align-items: center;
		.videojdt-l {
			width: 50rem;
			height: 50rem;
			font-size: 28rem;
			color: #fff;
			display: flex;
			justify-content: center;
			align-items: center;
		}
		.videojdt-m {
			position: relative;
			.videojdt-mt1 {
				width: 50rem;
				position: absolute;
				top: -5rem;
				left: 0;
				color: #fff;
			}
			.videojdt-mt2 {
				width: 50rem;
				position: absolute;
				top: -5rem;
				left: 275rem;
				color: #fff;
			}
			input {
				width: 200rem;
				position: absolute;
				top: -5rem;
				left: 50rem;
				z-index: 9;
				opacity: 0;
			}
			.videojdt-ma {
				position: absolute;
				top: 0;
				left: 50rem;
				z-index: 8;
				width: 200rem;
				height: 5rem;
				background-color: rgb(201, 201, 201);
				border-radius: 5rem;
				.videojdt-mb {
					height: 5rem;
					border-radius: 5rem;
					background-color: #fff;
				}
			}
		}
	}
	.videofan {
		position: absolute;
		left: 0;
		top: 10rem;
		border-radius: 0 10rem 10rem 0;
		z-index: 3;
		color: #fff;
		padding: 3rem 10rem;
		font-size: 18rem;
		background-color: rgba(255, 255, 255, 0.512);
	}

	.video-r {
		position: absolute;
		right: 20rem;
		bottom: 100rem;
		font-size: 28rem;
		color: #fff;
		z-index: 3;
		text-align: center;
		li {
			margin-top: 20rem;
			position: relative;
			color: #fff;
			.imgs {
				border: 1rem solid #fff;
				border-radius: 50%;
				width: 50rem;
				height: 50rem;
			}
			h5 {
				position: absolute;
				top: 35rem;
				left: 6rem;
				font-size: 10rem;
				color: #fff;
				background-color: #ff4466;
				padding: 1rem 6rem;
				border-radius: 10rem;
			}
			h6 {
				font-size: 10rem;
				color: #fff;
			}
		}
	}
	.video-name {
		position: absolute;
		bottom: 100rem;
		left: 22rem;
		z-index: 3;
		h2 {
			font-size: 16rem;
			height: 16rem;
			line-height: 16rem;
			color: #fff;
		}
		h3 {
			margin-top: 5rem;
			font-size: 10rem;
			color: rgb(176, 173, 173);
			samp {
				margin: 0 5rem;
			}
		}
	}

	.video-sp {
		display: flex;
		align-items: center;
		position: absolute;
		bottom: 30rem;
		left: 15rem;
		border-radius: 5rem;
		z-index: 3;
		width: 90%;
		padding: 5rem;
		background-color: rgba(0, 0, 0, 0.275);
		.video-sp-l {
			margin-right: 11rem;
			flex-grow: 0;
			flex-shrink: 0;
			width: 48rem;
			height: 48rem;
		}
		.video-sp-m {
			h5 {
				font-size: 14rem;
				color: #fff;
				font-weight: 400;
				height: 21rem;
				line-height: 21rem;
				overflow: hidden;
			}
			p {
				height: 25rem;
				font-size: 17rem;
				color: #fa4e82;
				font-weight: 600;
				display: flex;
				align-items: center;
				samp {
					background-color: #fb573a;
					color: #fff;
					font-weight: 400;
					margin-right: 5rem;
					font-size: 12rem;
					font-weight: 400;
				}
			}
		}
		.video-sp-r {
			flex-grow: 0;
			flex-shrink: 0;
			margin-left: 10rem;
			padding: 3rem 8rem;
			border-radius: 10rem;
			background-color: #ff4466;
			color: #fff;
		}
	}
}
</style>
