<template>
	<!-- 购物车界面 -->
	<div class="cart">
		<nav class="cart-nav">
			<div class="nav-l" @click="back"><van-icon name="arrow-left" /></div>
			<div class="nav-name">
				购物车<samp v-if="cart.length">({{ cart.length }})</samp>
			</div>
			<div class="nav-r" @click="deletecart"><van-icon name="delete-o" /></div>
		</nav>
		<ul>
			<li v-for="(n, i) in cart" :key="i" class="card">
				<div class="card-l" @click="togchecked">
					<van-checkbox v-model="n.judge" checked-color="#ee0a24"></van-checkbox>
				</div>
				<div class="card-r">
					<div
						class="card-r-img"
						:style="{
							backgroundImage: 'url(' + n.img + '_999x999.v1c0.81.webp)',
						}"></div>
					<div class="card-r-r">
						<h3>{{ n.name }}</h3>
						<h5>
							颜色 : <samp>{{ n.colo }} ; </samp> 尺码 : <samp>{{ n.size }}</samp>
						</h5>

						<div class="card-r-r-b">
							<div class="card-r-r-b-l">
								¥<samp>{{ n.price }}</samp>
							</div>
							<div class="card-r-r-b-r">
								<van-stepper v-model="value" integer :button-size="22" />
							</div>
						</div>
					</div>
				</div>
			</li>
		</ul>
		<footer class="foot">
			<div class="foot-l" @click="togall(cart)">
				<van-checkbox v-model="checked" checked-color="#ee0a24"></van-checkbox>
				<samp>全选 ({{ cart.length }})</samp>
			</div>
			<div class="foot-r">
				<div class="foot-r-r">
					¥<samp>{{ totalPrices }}</samp>
				</div>
				<button @click="$router.push({ path: '/order' })">结算</button>
			</div>
		</footer>
	</div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
	name: "ShoppingTrolleyView",

	data() {
		return {
			value: 1,
			checked: true,
			sen: 0,
		};
	},
	computed: {
		...mapState(["cart"]),
		totalPrices: function () {
			let s = 0;
			this.cart
				.map(function (o) {
					if (o.judge === true) {
						return o.price;
					} else {
						return 0;
					}
				})
				.forEach((o) => (s += Number(o)));
			return s;
		},
		checked2: function () {
			if (this.cart.find((o) => o.judge == false)) {
				return false;
			} else {
				return true;
			}
		},
	},

	mounted() {},

	methods: {
		...mapMutations(["deletecart"]),
		togchecked() {
			setTimeout(() => {
				this.checked = this.checked2;
			}, 0);
		},
		togall(cart) {
			setTimeout(() => {
				cart.forEach((o) => {
					o.judge = this.checked;
				});
			}, 0);
		},
		back() {
			if (window.history.length <= 1) {
				this.$router.push({ path: "/" });
				return false;
			} else {
				this.$router.go(-1);
			}
		},
	},
};
</script>

<style lang="less" scoped>
.cart {
	background-color: #efefef;
	.cart-nav {
		background-color: #fafafa;
		width: 100%;
		height: 45rem;
		border-bottom: 1px solid #c9c7c8;
		font-size: 18rem;
		display: flex;
		justify-content: space-between;
		color: rgb(94, 94, 94);
		.nav-l {
			width: 45rem;
			height: 44rem;
			display: flex;
			justify-content: center;
			align-items: center;
		}
		.nav-name {
			display: flex;
			justify-content: center;
			align-items: center;
			line-height: 44rem;
			color: rgb(94, 94, 94);
		}
		.nav-r {
			width: 45rem;
			height: 44rem;
			display: flex;
			justify-content: center;
			align-items: center;
		}
	}
	ul {
		padding: 20rem 15rem;
		margin: 9rem;
		background-color: #fff;
		.card {
			display: flex;
			margin-bottom: 17rem;
			.card-l {
				display: flex;
				justify-content: center;
				align-items: center;
				width: 20rem;
				margin-right: 9rem;
			}
			.card-r {
				display: flex;
				.card-r-img {
					flex-grow: 0;
					flex-shrink: 0;
					width: 69rem;
					height: 92rem;
					background-repeat: no-repeat;
					background-position: center;
					background-size: cover;
					border-radius: 3rem;
				}
				.card-r-r {
					flex-grow: 1;
					margin-left: 9rem;
					h3 {
						height: 32px;
						margin-bottom: 6rem;
						font-weight: 700;
						color: #333;
						font-size: 12rem;
						line-height: 16rem;
						overflow: hidden;
					}
					h5 {
						padding: 0 11rem 0 6rem;
						background-color: rgb(247, 247, 247);
						color: #ababab;
						height: 16rem;
						line-height: 16rem;
						font-size: 12rem;
						overflow: hidden;
					}
					.card-r-r-b {
						display: flex;
						justify-content: space-between;
						margin-top: 24rem;
						.card-r-r-b-l {
							font-size: 12rem;
							color: #f46;
							font-weight: 500;
							line-height: 17rem;
							samp {
								font-size: 16rem;
								font-weight: 700;
								line-height: 17rem;
							}
						}
						.card-r-r-b-r {
							height: 22rem;
						}
					}
				}
			}
		}
	}
	.foot {
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 50rem;
		padding: 10rem 15rem;
		border-top: 1px solid #d8d8d8;
		background-color: #fefefe;
		display: flex;
		justify-content: space-between;
		align-items: center;
		.foot-l {
			display: flex;
			align-items: center;
			samp {
				font-size: 14rem;
				font-weight: 400;
				line-height: 30rem;
				margin-left: 5rem;
				color: #5e5e5e;
			}
		}
		.foot-r {
			display: flex;
			.foot-r-r {
				font-size: 12rem;
				font-weight: 500;
				line-height: 20rem;
				color: #f46;
				margin-top: 7rem;
				samp {
					font-size: 16rem;
					font-weight: 700;
					line-height: 20rem;
				}
			}
			button {
				margin-left: 9rem;
				background-color: rgb(255, 87, 119);
				border: 0rem solid #fff;
				color: #fff;
				height: 28rem;
				line-height: 28rem;
				padding: 3rem 15rem;
				font-size: 19rem;
				border-radius: 15rem;
				display: flex;
				justify-content: center;
				align-items: center;
			}
		}
	}
}
</style>
