<template>
	<!-- 搜索界面 -->
	<div class="search">
		<!-- 顶部搜索 -->
		<nav>
			<img src="@/assets/jianto2.png" alt="" @click="back()" />
			<input
				type="text"
				placeholder="学院风jk套装"
				v-model="value"
				@keyup.enter="
					addsearchvalue(value),
						addhistorysearch(value),
						$router.push({ path: '/serp', query: { q: value } }),
						(value = '')
				" />
			<button
				@click="
					addsearchvalue(value),
						addhistorysearch(value),
						$router.push({ path: '/serp', query: { q: value } }),
						(value = '')
				">
				搜索
			</button>
		</nav>
		<!-- 历史搜索 -->
		<section class="history">
			<div class="chenghistory">
				<div class="chenghistory-l">
					<img src="@/assets/nuv3.png" alt="" />
					<samp>历史搜索</samp>
				</div>
				<div class="chenghistory-r" @click="deletehistorysearch()">
					<van-icon name="delete-o" />
				</div>
			</div>
			<ul>
				<p v-if="historysearch.length === 0">暂无历史搜索</p>
				<li
					v-else
					v-for="(n, i) in historysearch"
					:key="i"
					@click="
						addsearchvalue(n), $router.push({ path: '/serp', query: { q: n } })
					">
					{{ n }}
				</li>
			</ul>
		</section>
		<!-- 热门搜索 -->
		<section class="hotsearch">
			<div class="chenghotsearch">
				<div class="chenghotsearch-l">
					<img src="@/assets/nuv4.png" alt="" />
					<samp>热门搜索</samp>
				</div>
			</div>
			<ul v-if="hotsearch">
				<li
					v-for="(n, i) in hotsearch"
					:style="{ color: n.color }"
					:key="i"
					@click="
						addsearchvalue(n.query),
							addhistorysearch(n.query),
							$router.push({ path: '/serp', query: { q: n.query } })
					">
					{{ n.query }}
				</li>
			</ul>
		</section>
	</div>
</template>

<script>
import { mapMutations, mapState } from "vuex";

export default {
	name: "SearchView",

	data() {
		return {
			value: "",
		};
	},

	computed: {
		...mapState(["hotsearch", "searchvalue", "historysearch"]),
	},
	mounted() {},

	methods: {
		...mapMutations([
			"addhotsearch",
			"addsearchvalue",
			"addhistorysearch",
			"deletehistorysearch",
		]),
		back() {
			if (window.history.length <= 1) {
				this.$router.push({ path: "/" });
				return false;
			} else {
				this.$router.go(-1);
			}
		},
	},
	created() {
		//首页nuv
		this.axios
			.get("https://apis.netstart.cn/mogujie/hotSearch.json")
			.then((ren) => {
				this.addhotsearch(ren?.data?.data?.hotWord?.data);
			});
	},
};
</script>

<style lang="less" scoped>
.search {
	nav {
		width: 100%;
		height: 45rem;
		padding: 5rem 0;
		border-bottom: 1px solid #eee;
		display: flex;
		align-items: center;
		img {
			flex-shrink: 0;
			flex-grow: 0;
			width: 26.26rem;
			height: 20rem;
			margin-left: 7.5rem;
		}
		input {
			flex-grow: 1;
			height: 25rem;
			border-radius: 5rem;
			border: 1rem solid #aaa;
		}
		button {
			flex-shrink: 0;
			flex-grow: 0;
			width: 40rem;
			height: 25rem;
			font-size: 14rem;
			line-height: 25rem;
			color: #666;
			background-color: #fff;
			border: 0 solid #eee;
			text-align: center;
		}
	}
	.history {
		margin-top: 5rem;
		padding: 10rem;
		.chenghistory {
			display: flex;
			justify-content: space-between;
			height: 22.5rem;
			overflow: hidden;
			.chenghistory-l {
				display: flex;
				align-items: center;
				img {
					width: 15rem;
					height: 15rem;
					margin-right: 2rem;
				}
				samp {
					font-size: 12.5rem;
					height: 22.5rem;
					line-height: 22.5rem;
					color: #999;
					font-weight: 400;
				}
			}
			.chenghistory-r {
				font-size: 15rem;
				margin-right: 10rem;
				color: #999;
			}
		}
		ul {
			display: flex;
			flex-wrap: wrap;
			p {
				color: #999;
				font-size: 12.5rem;
				height: 22.5rem;
				line-height: 22.5rem;
				font-weight: 400;
				margin: 10rem 0 0 17.5rem;
			}
			li {
				padding: 0 11rem;
				height: 25rem;
				text-align: center;
				margin-left: 10rem;
				margin-top: 12.5rem;
				color: #666;
				line-height: 25rem;
				font-weight: 400;
				font-size: 12.5rem;
				border: 1px solid #f5f5f5;
				border-radius: 5rem;
			}
		}
	}
	.hotsearch {
		margin-top: 5rem;
		padding: 10rem;
		.chenghotsearch {
			height: 22.5rem;
			overflow: hidden;
			.chenghotsearch-l {
				display: flex;
				align-items: center;
				img {
					width: 15rem;
					height: 15rem;
					margin-right: 2rem;
				}
				samp {
					font-size: 12.5rem;
					height: 22.5rem;
					line-height: 22.5rem;
					color: #999;
					font-weight: 400;
				}
			}
		}
		ul {
			display: flex;
			flex-wrap: wrap;
			li {
				width: 47rem;
				height: 25rem;
				text-align: center;
				margin-left: 10rem;
				margin-top: 12.5rem;
				color: #666;
				line-height: 25rem;
				font-weight: 400;
				font-size: 12.5rem;
				border: 1px solid #f5f5f5;
				border-radius: 5rem;
			}
		}
	}
}
</style>
