<template>
	<!-- 商品分类界面 -->
	<div class="chengbox">
		<nav class="chengnuv">
			<div class="chengnuv-l" @click="back()"></div>
			<div class="chengnuv-m" @click="$router.push({ path: '/search' })">
				<img src="@/assets/nuv3.png" alt="" />
				<input type="text" pattern="学院风jk套装" />
			</div>
			<div class="chengnuv-r" @click="$router.push({ path: '/shoppingtrolley' })"></div>
		</nav>
		<div class="chengstorelistmax">
			<van-sidebar v-model="activeKey" class="chengstorelista" @change="onChange">
				<van-sidebar-item
					class="chengstorelist"
					v-for="n in storelist"
					:key="n._system_record_entry_id"
					:title="n.title" />
			</van-sidebar>

			<div class="chengstorelistmax-l">
				<ul class="chengstorelistmax-l-t">
					<StoreListnav
						v-for="n in storelistclassify"
						:key="n?._system_record_entry_id"
						:n="n" />
				</ul>
				<div class="chengstorelistmax-l-b">
					<div class="chengstorelistmax-l-b-t">
						<div
							class="chengstorelistmax-l-b-t-l"
							:class="{ chenga: chenga === 1 }"
							@click="chenga = 1">
							综合
						</div>
						<div
							class="chengstorelistmax-l-b-t-m"
							:class="{ chenga: chenga === 2 }"
							@click="chenga = 2">
							销量
						</div>
						<div
							class="chengstorelistmax-l-b-t-r"
							:class="{ chenga: chenga === 3 }"
							@click="chenga = 3">
							新品
						</div>
					</div>
					<div class="chengstorelistmax-l-b-b">
						<ul v-if="chenga === 1">
							<StoreListMax
								v-for="n in storelistsynthesize"
								:key="n.tradeItemId"
								:n="n" />
						</ul>
						<ul v-if="chenga === 2">
							<StoreListMax v-for="n in storelistsales" :key="n.tradeItemId" :n="n" />
						</ul>
						<ul v-if="chenga === 3">
							<StoreListMax v-for="n in storelistnew" :key="n.tradeItemId" :n="n" />
						</ul>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import StoreListnav from "@/components/StoreListnav.vue";
import StoreListMax from "@/components/StoreListMax.vue";

export default {
	name: "StoreListView",

	components: {
		StoreListnav,
		StoreListMax,
	},
	data() {
		return {
			activeKey: 0,
			getSub: 3627,
			chenga: 1,
			chnegminiWallkey: 10062603,
		};
	},
	computed: {
		...mapState([
			"storelist",
			"storelistclassify",
			"storelistsynthesize",
			"storelistsales",
			"storelistnew",
		]),
	},

	mounted() {},

	methods: {
		...mapMutations([
			"addstorelist",
			"addstorelistclassify",
			"addstorelistsynthesize",
			"addstorelistsales",
			"addstorelistnew",
		]),
		onChange(index) {
			this.getSub = this.storelist[index].maitKey;
			this.chnegminiWallkey = this.storelist[index].miniWallkey;
		},

		back() {
			if (window.history.length <= 1) {
				this.$router.push({ path: "/" });
				return false;
			} else {
				this.$router.go(-1);
			}
		},
	},

	created() {
		//左边导航栏
		this.axios
			.get("https://apis.netstart.cn/mogujie/category.json")
			.then((ren) => {
				this.addstorelist(ren?.data?.data[117330].list);
			});

		//右边上
		this.axios
			.get("https://apis.netstart.cn/mogujie/category/getSub", {
				params: {
					pid: this.getSub,
				},
			})
			.then((ren) => {
				this.addstorelistclassify(ren?.data?.data?.list);
			});

		//右边下
		this.axios
			.get("https://apis.netstart.cn/mogujie/category/miniWall", {
				params: {
					fcid: this.chnegminiWallkey,
					page: 1,
					sort: "new",
				},
			})
			.then((ren) => {
				this.addstorelistsynthesize(ren?.data?.result?.wall.docs);
			});

		this.axios
			.get("https://apis.netstart.cn/mogujie/category/miniWall", {
				params: {
					fcid: this.chnegminiWallkey,
					page: 1,
					sort: "sell",
				},
			})
			.then((ren) => {
				this.addstorelistsales(ren?.data?.result?.wall.docs);
			});

		this.axios
			.get("https://apis.netstart.cn/mogujie/category/miniWall", {
				params: {
					fcid: this.chnegminiWallkey,
					page: 1,
					sort: "pop",
				},
			})
			.then((ren) => {
				this.addstorelistnew(ren?.data?.result?.wall.docs);
			});
	},

	watch: {
		getSub(newValue) {
			//右边上
			this.axios
				.get("https://apis.netstart.cn/mogujie/category/getSub", {
					params: {
						pid: newValue,
					},
				})
				.then((ren) => {
					this.addstorelistclassify(ren?.data?.data?.list);
				});
		},
		chnegminiWallkey(newValue) {
			//右边下
			this.axios
				.get("https://apis.netstart.cn/mogujie/category/miniWall", {
					params: {
						fcid: newValue,
						page: 1,
						sort: "new",
					},
				})
				.then((ren) => {
					this.addstorelistsynthesize(ren?.data?.result?.wall.docs);
				});

			this.axios
				.get("https://apis.netstart.cn/mogujie/category/miniWall", {
					params: {
						fcid: newValue,
						page: 1,
						sort: "sell",
					},
				})
				.then((ren) => {
					this.addstorelistsales(ren?.data?.result?.wall.docs);
				});

			this.axios
				.get("https://apis.netstart.cn/mogujie/category/miniWall", {
					params: {
						fcid: newValue,
						page: 1,
						sort: "pop",
					},
				})
				.then((ren) => {
					this.addstorelistnew(ren?.data?.result?.wall.docs);
				});
		},
	},
};
</script>

<style lang="less" scoped>
.cheng {
	background-color: #000;
}
.van-sidebar-item--select {
	color: #ff5777;
	font-size: 14rem;
	font-weight: 700;
}
.chengnuv {
	display: flex;
	justify-content: space-between;
	padding: 7.5rem 5rem;
	border-bottom: 1px solid #e5e5e5;
	background-color: #fff;
	position: sticky;
	top: 0;
	left: 0;
	z-index: 2;
	.chengnuv-l {
		width: 50rem;
		height: 30rem;
		text-align: center;
		background-image: url(@/assets/jianto2.png);
		background-size: contain;
		background-position: center;
		background-repeat: no-repeat;
		transform: scale(0.8);
	}
	.chengnuv-m {
		display: flex;
		flex-grow: 1;
		background-color: #eeeeee;
		img {
			width: 30rem;
			height: 30rem;
			transform: scale(0.6);
		}
		input {
			width: 100%;
			height: 30rem;
			border: 0px solid red;
			background-color: #eee;
		}
	}
	.chengnuv-r {
		width: 50rem;
		height: 30rem;
		text-align: center;
		background-image: url(@/assets/gwc.png);
		background-size: contain;
		background-position: center;
		background-repeat: no-repeat;
		transform: scale(0.8);
	}
}

.chengstorelistmax {
	display: flex;
	.chengstorelista {
		width: 90rem;
		height: 100vh;
		flex-grow: 0;
		flex-shrink: 0;
		position: sticky;
		top: 0;
		left: 0;
		.chengstorelist {
			height: 45rem;
			padding: 14rem 12rem;
			text-align: center;
		}
	}
	.chengstorelistmax-l {
		flex-grow: 0;
		flex-shrink: 0;
		width: 285rem;
		.chengstorelistmax-l-t {
			display: flex;
			flex-wrap: wrap;
			width: 100%;
			// height: 377.5rem;
			padding: 15rem 10rem 17.5rem 10rem;
		}
		.chengstorelistmax-l-b {
			.chengstorelistmax-l-b-t {
				height: 39rem;
				border-bottom: 1px solid #eaeaea;
				display: flex;
				div {
					width: 33.33%;
					height: 39rem;
					line-height: 39rem;
					text-align: center;
				}
				.chenga {
					color: #ef4562;
				}
			}
			.chengstorelistmax-l-b-b {
				padding: 5.7rem;
				ul {
					display: flex;
					flex-wrap: wrap;
					justify-content: space-between;
				}
			}
		}
	}
}

.van-tab {
	height: 39rem;
}
</style>
