import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import LiveView from '../views/LiveView.vue'
import StoreView from '../views/StoreView.vue'
import MeView from '../views/MeView.vue'
import StoreListView from '../views/StoreListView.vue'
import VideoView from '../views/VideoView.vue'
import SearchView from '../views/SearchView.vue'
import ShoppingTrolleyView from '../views/ShoppingTrolleyView.vue'
import SERPView from '../views/SERPView.vue'
import CommodityView from '../views/CommodityView.vue'
import DBRView from '../views/DBRView.vue'
import OrderView from '../views/OrderView.vue'
import ListView from '../views/ListView.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    meta: { showNav: true },
    component: HomeView
  },
  {
    path: '/live',
    name: 'live',
    meta: { showNav: true },
    component:LiveView
  },
  {
    path: '/store',
    name: 'store',
    meta: { showNav: true },
    component:StoreView
  },
  {
    path: '/me',
    name: 'me',
    meta: { showNav: true },
    component:MeView
  },
  {
    path: '/storelist',
    name: 'storelist',
    component:StoreListView
  },
  {
    path: '/video',
    name: 'video',
    component:VideoView
  },
  {
    path: '/search',
    name: 'search',
    component:SearchView
  },
  {
    path: '/shoppingtrolley',
    name: 'shoppingtrolley',
    component:ShoppingTrolleyView
  },
  {
    path:'/serp',
    name: 'serp',
    component:SERPView
  },
  {
    path:'/commodity',
    name: 'commodity',
    component:CommodityView
  },
  {
    path:'/dbr',
    name: 'dbr',
    component:DBRView
  },
  {
    path:'/order',
    name: 'order',
    component:OrderView
  },
  {
    path:'/list',
    name: 'list',
    component:ListView
  },
]

const router = new VueRouter({
  routes
})

export default router
