<template>
	<!-- 商品详情界面 -->
	<div class="commodity">
		<nav class="commoditynav" v-if="commodity">
			<van-swipe
				v-if="swipepd"
				:loop="false"
				:width="270"
				:height="390"
				ref="swipe1"
				:show-indicators="false">
				<van-swipe-item
					class="commodityimg"
					v-for="(n, i) in commodity?.topImages"
					:key="i">
					<img :src="n + '_999x999.v1c0.81.webp'" alt="" />
				</van-swipe-item>
			</van-swipe>
			<van-swipe
				v-if="!swipepd"
				:loop="false"
				:width="270"
				ref="swipe2"
				:show-indicators="false">
				<van-swipe-item
					class="commodityimg"
					v-for="n in commodity?.skuBarInfo?.list"
					:key="n.stockId">
					<img :src="n?.image + '_999x999.v1c0.81.webp'" alt="" />
				</van-swipe-item>
			</van-swipe>

			<div class="commoditynav-b" v-if="commodity">
				<div class="commoditynav-b-l" @click="swipepd = true">
					<div class="commoditynav-b-l-l">
						<img
							:src="commodity?.normalShareInfo?.imageUrl + '_999x999.v1c0.81.webp'"
							alt="" />
					</div>
					<div class="commoditynav-b-l-r">颜色规格</div>
				</div>
				<ul class="commoditynav-b-r">
					<li
						v-for="(n, i) in commodity?.skuBarInfo?.list"
						:key="n.stockId"
						@click="chengg(i)">
						<img
							:src="commodity?.skuBarInfo?.list[i].image + '_999x999.v1c0.81.webp'"
							alt="" />
					</li>
				</ul>
			</div>
		</nav>
		<section class="commodityprice" v-if="commodity">
			<div class="commodityprice-top">
				<div class="commodityprice-top-l">
					<div class="commodityprice-top-l-l">
						¥<samp>{{ commodity?.normalPriceV2?.nowPrice }}</samp>
					</div>
					<samp v-if="commodity?.normalPriceV2?.priceTags.length !== 0">{{
						commodity?.normalPriceV2?.priceTags[0].text
					}}</samp>
				</div>
				<div class="commodityprice-top-r">
					已售{{ commodity?.normalPriceV2?.sales }}
				</div>
			</div>
			<!-- <div class="commodityprice-discounts">
				<div class="commodityprice-discounts-l"></div>
				<div class="commodityprice-discounts-r">领劵</div>
			</div> -->
			<div class="commodityprice-name">
				<div class="commodityprice-name-l">{{ commodity?.itemInfo?.title }}</div>
				<div class="commodityprice-name-r"><van-icon name="friends-o" />帮我选</div>
			</div>
		</section>
		<section class="commoditysiez" v-if="commodity">
			<van-cell is-link @click="showPopup2">选择</van-cell>
			<van-cell>免邮费</van-cell>
			<van-cell is-link @click="showPopup">
				服务
				<samp>7天无理由退货</samp>| <samp>延误必赔</samp>|
				<samp>退货补运费</samp>
			</van-cell>
			<van-action-sheet :round="false" v-model="show" title="服务说明">
				<div class="content chengcommodity">
					<div
						class="chengcommodity-p"
						v-for="n in commodity?.shopInfo?.services"
						:key="n?.services">
						<h5>{{ n?.name }}</h5>
						<p>{{ n?.desc }}</p>
					</div>
				</div>
			</van-action-sheet>
		</section>
		<section
			class="commodityremark"
			v-if="commodity && commodity?.rateInfoV2?.cRate !== 0">
			<van-cell :title="'评论(' + commodity?.rateInfoV2?.cRate + ')'" is-link>
				评分<samp>{{ commodity?.rateInfoV2?.cScore }}</samp>
			</van-cell>
			<ul class="commodityremarkcard">
				<li v-for="n in commodity?.rateInfoV2?.rateTags" :key="n.labelId">
					{{ n.value }}({{ n.num }})
				</li>
			</ul>
			<van-swipe :loop="false" :width="310" :height="108" :show-indicators="false">
				<van-swipe-item v-for="n in commodity?.rateInfoV2?.list" :key="n.rateId">
					<div class="card">
						<div class="card-t">
							<img :src="n?.user?.avatar + '_999x999.v1c0.81.webp'" alt="" />
							<div class="card-t-r">
								<h5>{{ n?.user?.uname }}</h5>
								<p>{{ n?.created | formatDate }}</p>
							</div>
						</div>
						<div class="card-b">
							{{ n?.content }}
						</div>
					</div>
				</van-swipe-item>
			</van-swipe>
		</section>
		<section class="commoditystore" v-if="commodity">
			<div class="store-l">
				<img
					:src="commodity?.shopInfo?.shopLogo + '_999x999.v1c0.81.webp'"
					alt="" />
				<div class="store-l-r">
					<h3>{{ commodity?.shopInfo?.name }}</h3>
					<h4>
						<van-icon name="star" /><samp>{{ commodity?.shopInfo?.saleDescV2 }}</samp>
					</h4>
					<ul>
						<li v-for="n in commodity?.shopInfo?.shopLabels" :key="n.id">
							{{ n.text }}
						</li>
					</ul>
				</div>
			</div>
			<div class="store-r">进店<van-icon name="arrow" /></div>
		</section>
		<section class="commoditydetails" v-if="commodity">
			<div class="details-top">
				<div class="details-top-l">图文详情</div>
				<div class="details-top-r">商品参数</div>
			</div>
			<div class="details-m">
				{{ commodity?.detailInfo?.desc }}
			</div>
			<div class="datails-b">
				<ul>
					<li v-for="(n, i) in commodity?.detailInfo?.detailImage[0].list" :key="i">
						<img :src="n + '_999x999.v1c0.81.webp'" alt="" />
					</li>
				</ul>
			</div>
		</section>
		<footer class="commodityfot" v-if="commodity">
			<div class="commodityfot-1">
				<van-icon name="shop-o" />
				<p>店铺</p>
			</div>
			<div class="commodityfot-1" style="color: rgb(255, 68, 102)">
				<van-icon name="chat-o" />
				<p>客服</p>
			</div>
			<div class="commodityfot-1">
				<van-icon name="star-o" />
				<p>收藏</p>
			</div>
			<div class="commodityfot-2" @click="showPopup2">加入购物车</div>
			<div class="commodityfot-3" @click="showPopup2">立即购买</div>
		</footer>
		<div class="box">
			<div class="home" @click="$router.push({ path: '/' })">
				<van-icon name="wap-home-o" />
			</div>
			<div class="gwc" @click="$router.push({ path: '/shoppingtrolley' })">
				<van-icon name="shopping-cart-o" />
			</div>
		</div>
		<van-action-sheet :round="false" v-model="show2" v-if="commodity">
			<div class="chengcontent">
				<div class="chengcontent-top">
					<div class="chengcontent-top-l">
						<div
							class="chengcontent-top-l-l"
							:style="{
								backgroundImage: 'url(' + chengimg + '_999x999.v1c0.81.webp)',
							}">
							<!-- <img :src=" + ''" alt="" /> -->
						</div>
						<div class="chengcontent-top-l-r">
							<h3>{{ commodity?.skuInfo?.priceRange }}</h3>
							<h5>
								库存:
								<samp
									>{{
										chengkc
											? chengkc
											: chengrepertory1
											? chengrepertory1
											: chengrepertory2
											? chengrepertory2
											: chengrepertory
									}}
									件</samp
								>
							</h5>
							<p>
								请选择
								<samp> {{ ccolo }}</samp>
								<samp> {{ csize }}</samp>
							</p>
						</div>
					</div>
					<div class="chengcontent-top-r">
						<samp @click="showPopup2x"><van-icon name="cross" /></samp>
					</div>
				</div>

				<!-- 颜色 -->
				<div class="chengcontent-1" v-if="commodity?.skuInfo?.props.length">
					<h6>{{ commodity?.skuInfo?.props[0].label }}</h6>
					<ul>
						<li
							v-for="(n, i) in commodity?.skuInfo?.props[0].list"
							:key="n.index"
							@click="
								chenggl1 = i;
								ccolo = n.name;
								alterimg(n.name);
								alterchengrepertory1(n.name);
							"
							:class="{ chenggl1: chenggl1 === i }">
							{{ n.name }}
						</li>
					</ul>
				</div>

				<!-- 尺寸 -->
				<div class="chengcontent-2" v-if="commodity?.skuInfo?.props.length === 2">
					<h6>{{ commodity?.skuInfo?.props[1].label }}</h6>
					<ul>
						<li
							v-for="(n, i) in commodity?.skuInfo?.props[1].list"
							@click="
								chenggl2 = i;
								csize = n.name;
								alterchengrepertory2(n.name);
							"
							:class="{ chenggl2: chenggl2 === i }"
							:key="n.index">
							{{ n.name }}
						</li>
					</ul>
				</div>

				<!-- 数量 -->
				<div class="chengcontent-3">
					<h6>数量</h6>
					<van-stepper v-model="chengquantity" integer />
				</div>
				<div class="chengcontent-but">
					<button class="chengcontent-but-l" @click="addgwc">加入购物车</button>
					<button class="chengcontent-but-r" @click="addgqw">立即购买</button>
				</div>
			</div>
		</van-action-sheet>
	</div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import { formatDate } from "@/js/time.js";

export default {
	name: "CommodityView",

	data() {
		return {
			swipepd: true,
			getid: this.$route.query.id,
			show: false,
			show2: false,

			// 购买界面点击高亮
			chenggl1: null,
			chenggl2: null,

			// 购买界面库存
			chengquantity: 1,
			chengrepertory: 0,
			chengrepertory1: null,
			chengrepertory2: null,

			// 购买界面尺寸显示
			csize: "",
			ccolo: "",

			// 购买界面图片
			chengimg: null,
		};
	},
	computed: {
		...mapState(["commodity", "cart"]),
		chengkc: function () {
			if (
				this.ccolo &&
				this.csize &&
				this.csize !== "尺码" &&
				this.ccolo !== "颜色"
			) {
				console.log("两个都存在");
				return this.commodity.skuInfo.skus.find(
					(o) => o.style == this.ccolo && o.size == this.csize
				).stock;
			} else {
				return null;
			}
		},
	},

	mounted() {},

	methods: {
		...mapMutations(["addcommodity", "addcart"]),
		chengg(i) {
			this.swipepd = false;
			setTimeout(() => {
				this.$refs.swipe2.swipeTo(i);
			}, 0);
		},
		showPopup() {
			this.show = true;
		},
		showPopup2() {
			this.show2 = true;
		},
		showPopup2x() {
			this.show2 = false;
		},
		alterimg(n) {
			this.chengimg = this.commodity.skuInfo.skus.find((o) => o.style == n).img;
		},
		alterchengrepertory1(n) {
			var s = 0;
			this.chengrepertory1 = this.commodity.skuInfo.skus
				.map((o) => {
					if (o.style !== n) {
						return o.stock;
					}
					return 0;
				})
				.forEach(function (ss) {
					s += ss;
				});
			this.chengrepertory1 = s;
		},
		alterchengrepertory2(n) {
			var s1 = 0;
			this.chengrepertory2 = this.commodity.skuInfo.skus
				.map((o) => {
					if (o.size !== n) {
						return o.stock;
					}
					return 0;
				})
				.forEach(function (ss) {
					s1 += ss;
				});
			this.chengrepertory2 = s1;
		},
		addgwc() {
			if (
				this.ccolo &&
				this.csize &&
				this.csize !== "尺码" &&
				this.ccolo !== "颜色"
			) {
				console.log("添加购物车");
				this.addcart({
					img: this.chengimg,
					name: this.commodity?.itemInfo?.title,
					chengquantity: this.chengquantity,
					price: this.commodity?.normalPriceV2?.nowPrice,
					size: this.csize,
					colo: this.ccolo,
					id: this.getid,
					judge: true,
				});
			} else {
				alert("请选择规格");
			}
		},
		addgqw() {
			if (
				this.ccolo &&
				this.csize &&
				this.csize !== "尺码" &&
				this.ccolo !== "颜色"
			) {
				console.log("添加购物车");
				this.addcart({
					img: this.chengimg,
					name: this.commodity?.itemInfo?.title,
					chengquantity: this.chengquantity,
					price: this.commodity?.normalPriceV2?.nowPrice,
					size: this.csize,
					colo: this.ccolo,
					id: this.getid,
					judge: true,
				});
				this.$router.push({ path: "/order" });
			} else {
				alert("请选择规格");
			}
		},
	},
	created() {
		this.axios
			.get("https://apis.netstart.cn/mogujie/goods/detail", {
				params: {
					itemId: this.getid,
				},
			})
			.then((ren) => {
				this.addcommodity(ren?.data?.data?.result);
				this.chengrepertory = ren?.data?.data?.result.skuInfo?.totalStock;
				this.csize = ren?.data?.data?.result?.skuInfo?.sizeKey;
				this.ccolo = ren?.data?.data?.result?.skuInfo?.styleKey;
				this.chengimg = ren?.data?.data?.result?.webImOptionInfo?.option?.img;
			});
	},
	watch: {
		getid(newValue) {
			this.axios
				.get("https://apis.netstart.cn/mogujie/goods/detail", {
					params: {
						itemId: newValue,
					},
				})
				.then((ren) => {
					this.addcommodity(ren?.data?.data?.result);
					this.chengrepertory = ren?.data?.data?.result.skuInfo?.totalStock;
					this.csize = ren?.data?.data?.result?.skuInfo?.sizeKey;
					this.ccolo = ren?.data?.data?.result?.skuInfo?.styleKey;
					this.chengimg = ren?.data?.data?.result?.webImOptionInfo?.option?.img;
				});
		},
	},
	filters: {
		formatDate(time) {
			time = time * 1000;
			let date = new Date(time);
			return formatDate(date, "yyyy-MM-dd hh:mm");
		},
	},
};
</script>

