		padding: 15rem 12rem;
		margin-bottom: 9rem;
		background-color: #fff;
		.store-l {
			display: flex;
			img {
				width: 60rem;
				height: 60rem;
				margin-right: 10rem;
			}
			.store-l-r {
				h3 {
					font-size: 14rem;
					height: 14rem;
					line-height: 14rem;
