<template>
	<!-- 商城界面 -->
	<div class="store">
		<div class="box">
			<img src="@/assets/logo.png" alt="" />
			<div class="box-m">
				<h1>蘑菇街 总有高手帮你挑</h1>
				<h3>
					<img src="@/assets/xing.png" alt="" />
					<img src="@/assets/xing.png" alt="" />
					<img src="@/assets/xing.png" alt="" />
					<img src="@/assets/xing.png" alt="" />
					<img src="@/assets/xing.png" alt="" />
					<samp>超过2亿用户已下载</samp>
				</h3>
			</div>
			<van-button class="box-r" type="primary" size="mini" color="#ff4466"
				>前往查看</van-button
			>
		</div>
		<nav class="chengnuv">
			<div class="chengnuv-l" @click="$router.push({ path: '/storelist' })"></div>
			<div class="chengnuv-m" @click="$router.push({ path: '/search' })">
				<img src="@/assets/nuv3.png" alt="" />
				<input type="text" pattern="学院风jk套装" />
			</div>
			<div class="chengnuv-r"></div>
		</nav>
		<section class="storem">
			<ul>
				<StoreNav
					v-for="n in chengstore"
					:key="n?._system_record_entry_id"
					:n="n" />
			</ul>
		</section>
		<section class="storeyoulove" v-if="chengyoulove">
			<img src="@/assets/youlove.webp" alt="" />
			<ul>
				<van-list
					v-model="loading"
					:finished="finished"
					finished-text="没有更多了"
					@load="onLoad">
					<StoreYouLove v-for="n in chengyoulove" :key="n?.iid" :n="n" />
				</van-list>
			</ul>
		</section>
	</div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import StoreNav from "@/components/StoreNav.vue";
import StoreYouLove from "@/components/StoreYouLove.vue";

export default {
	name: "StoreView",

	components: {
		StoreNav,
		StoreYouLove,
	},

	data() {
		return {
			loading: false,
			finished: false,
			pages: 1,
		};
	},

	mounted() {},

	computed: {
		...mapState(["chengstore", "chengyoulove"]),
	},

	methods: {
		...mapMutations(["addchengstore", "addchengyoulove", "pushchengyoulove"]),

		onLoad() {
			console.log("触底了");
			this.pages++;
			this.axios
				.get("https://apis.netstart.cn/mogujie/goods/personalized", {
					params: {
						page: this.pages,
					},
				})
				.then((ren) => {
					console.log(ren?.data?.data?.wall?.docs);
					this.pushchengyoulove(ren?.data?.data?.wall?.docs);
					this.loading = false;
					if (this.pages >= 3) {
						this.finished = true;
					}
				});
		},
	},

	created() {
		//商城nuv
		this.axios
			.get("https://apis.netstart.cn/mogujie/shopNav.json")
			.then((ren) => {
				this.addchengstore(ren?.data);
			});
		//商城列表 猜你喜欢
		this.axios
			.get("https://apis.netstart.cn/mogujie/goods/personalized", {
				params: {
					page: this.pages,
				},
			})
			.then((ren) => {
				// console.log(ren.data.data.list);
				this.addchengyoulove(ren?.data?.data?.wall?.docs);
			});
	},
};
</script>

<style lang="less" scoped>
.store {
	.box {
		display: flex;
		padding: 8.5rem 15rem;
		border-bottom: 1px solid #c5c5c5;
		img {
			width: 30rem;
			height: 30rem;
		}
		.box-m {
			margin-left: 11rem;
			flex-grow: 1;
			h1 {
				font-size: 13rem;
				line-height: 15rem;
			}
			h3 {
				margin-top: 3rem;
				img {
					width: 9rem;
					height: 8.5rem;
					margin-right: 4rem;
				}
				samp {
					color: #8e8e92;
					font-size: 12rem;
					margin-left: 10rem;
				}
			}
		}
		.box-r {
			flex-grow: 0;
			width: 75rem;
			height: 25px;
		}
	}
	.chengnuv {
		display: flex;
		justify-content: space-between;
		padding: 7.5rem 5rem;
		border-bottom: 1px solid #e5e5e5;
		background-color: #fff;
		.chengnuv-l {
			width: 50rem;
			height: 30rem;
			text-align: center;
			background-image: url(@/assets/nuv1.png);
			background-size: contain;
			background-position: center;
			background-repeat: no-repeat;
			transform: scale(0.8);
		}
		.chengnuv-m {
			display: flex;
			flex-grow: 1;
			background-color: #eeeeee;
			img {
				width: 30rem;
				height: 30rem;
				transform: scale(0.6);
			}
			input {
				width: 100%;
				height: 30rem;
				border: 0px solid red;
				background-color: #eee;
			}
		}
		.chengnuv-r {
			width: 50rem;
			height: 30rem;
			text-align: center;
			background-image: url(@/assets/nuv2.png);
			background-size: contain;
			background-position: center;
			background-repeat: no-repeat;
			transform: scale(0.8);
		}
	}
	.storem {
		height: 100rem;
		background-color: #fff;
		ul {
			display: flex;
			padding: 9rem 0 9rem 9rem;
		}
	}
	.storeyoulove {
		background-color: #efefef;
		img {
			width: 100%;
			height: 100%;
		}
		ul {
			.van-list {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
			}
			padding: 9rem;
			background-color: #efefef;
		}
	}
}
</style>
