<template>
	<!-- 直播列表 -->
	<div class="live">
		<div class="box">
			<img src="@/assets/logo.png" alt="" />
			<div class="box-m">
				<h1>蘑菇街 总有高手帮你挑</h1>
				<h3>
					<img src="@/assets/xing.png" alt="" />
					<img src="@/assets/xing.png" alt="" />
					<img src="@/assets/xing.png" alt="" />
					<img src="@/assets/xing.png" alt="" />
					<img src="@/assets/xing.png" alt="" />
					<samp>超过2亿用户已下载</samp>
				</h3>
			</div>
			<van-button class="box-r" type="primary" size="mini" color="#ff4466"
				>前往查看</van-button
			>
		</div>
		<van-tabs v-model="activeName" v-if="list" animated swipeable>
			<van-tab v-for="n in list" :title="n.name" :name="n.id" :key="n.id">
				<ul v-if="lovelis" class="lives">
					<li
						v-for="(n, i) in lovelis"
						:key="i"
						class="card"
						@click="
							$router.push({
								path: '/dbr',
								query: { roomId: n.roomId, actUserId: n.actUserId },
							})
						">
						<div class="card-top">
							<img :src="n.avatar + '_100x100.jpg'" alt="" />
							<div class="card-top-name">{{ n.userName }}</div>
						</div>
						<div
							class="card-bot"
							:style="{ backgroundImage: 'url(' + n.bgImg + '_750x500.jpg)' }">
							<div class="card-yy"></div>
						</div>
					</li>
				</ul>
			</van-tab>
		</van-tabs>
	</div>
</template>

<script>
export default {
	name: "LiveView",

	data() {
		return {
			activeName: "1",
			list: null,
			lovelis: null,
			listid: 1,
		};
	},

	mounted() {},

	methods: {
		getlovelis() {
			this.axios
				.get("https://apis.netstart.cn/mogujie/live/tabList", {
					params: {
						tabId: this.activeName,
					},
				})
				.then((res) => {
					this.lovelis = res?.data?.data?.lives;
				});
		},
	},

	created() {
		this.axios
			.get("https://apis.netstart.cn/mogujie/live/tabHeader")
			.then((ren) => {
				this.list = ren?.data?.data.tabs.items;
			});
		this.axios
			.get("https://apis.netstart.cn/mogujie/live/tabList", {
				params: {
					tabId: this.activeName,
					pageIndex:1,
				},
			})
			.then((res) => {
				this.lovelis = res?.data?.data?.lives;
			});
	},
	watch: {
		activeName() {
			this.getlovelis();
		},
	},
};
</script>

<style lang="less" scoped>
.live {
	.box {
		display: flex;
		padding: 8.5rem 15rem;
		border-bottom: 1px solid #c5c5c5;
		img {
			width: 30rem;
			height: 30rem;
		}
		.box-m {
			margin-left: 11rem;
			flex-grow: 1;
			h1 {
				font-size: 13rem;
				line-height: 15rem;
			}
			h3 {
				margin-top: 3rem;
				img {
					width: 9rem;
					height: 8.5rem;
					margin-right: 4rem;
				}
				samp {
					color: #8e8e92;
					font-size: 12rem;
					margin-left: 10rem;
				}
			}
		}
		.box-r {
			flex-grow: 0;
			width: 75rem;
			height: 25px;
		}
	}
	.lives {
		padding: 0 12rem;
		.card {
			.card-top {
				display: flex;
				height: 42rem;
				img {
					box-sizing: content-box;
					width: 43rem;
					height: 43rem;
					margin: 0 9rem 0 12rem;
					border: 2rem solid hsla(0, 0%, 100%, 0.9);
					transform: translateY(20%);
					border-radius: 50%;
				}
				.card-top-name {
					height: 42rem;
					color: #333;
					font-size: 14rem;
					font-weight: 500;
					line-height: 55rem;
				}
			}
			.card-bot {
				width: 100%;
				height: 200rem;
				border-radius: 4rem;
				background-position: center;
				background-repeat: no-repeat;
				background-size: cover;
				position: relative;
				z-index: -1;
				.card-yy {
					position: absolute;
					bottom: 0;
					left: 0;
					width: 100%;
					height: 113.5rem;
					background-image: linear-gradient(180deg, transparent, rgba(0, 0, 0, 0.4));
				}
			}
		}
	}
}
</style>
