<template>
	<!-- 搜索结果界面 -->
	<div class="SERPView">
		<div class="box">
			<nav class="chengnuv">
				<div class="chengnuv-l" @click="back()">
					<div class="chengnuv-l-m"></div>
				</div>
				<div
					class="chengnuv-m"
					@click="$router.push({ path: '/search' }), addhistorysearch(searchvalue)">
					<img src="@/assets/nuv3.png" alt="" />
					<input type="text" :value="this.$route.query.q" />
					<img src="@/assets/nuv5.png" alt="" />
				</div>
				<div class="chengnuv-r" @click="$router.push({ path: '/shoppingtrolley' })">
					<div class="chengnuv-r-m"></div>
				</div>
			</nav>
			<section class="serp-tab">
				<div class="tab1" :class="{ cheng: chengg === 1 }" @click="chengg = 1">
					综合
				</div>
				<div class="tab2" :class="{ cheng: chengg === 2 }" @click="chengg = 2">
					销量
				</div>
				<div class="tab3" :class="{ cheng: chengg === 3 }" @click="chengg = 3">
					新品
				</div>
				<div class="tab4" :class="{ cheng: chengf }" @click="chengf = !chengf">
					价格<van-icon name="arrow-down" />
				</div>
				<transition name="van-fade">
					<div class="chengtab" v-show="chengf">
						<ul v-if="SERPpop?.priceFilter">
							<li
								@click="
									(jiage1 = SERPpop?.priceFilter[2].min),
										(jiage2 = SERPpop?.priceFilter[2].max),
										chengp()
								">
								{{ SERPpop?.priceFilter[0].min }}-{{ SERPpop?.priceFilter[0].max }}
							</li>
							<li
								@click="
									(jiage1 = SERPpop?.priceFilter[2].min),
										(jiage2 = SERPpop?.priceFilter[2].max),
										chengp()
								">
								{{ SERPpop?.priceFilter[1].min }}-{{ SERPpop?.priceFilter[1].max }}
							</li>
							<li
								@click="
									(jiage1 = SERPpop?.priceFilter[2].min),
										(jiage2 = SERPpop?.priceFilter[2].max),
										chengp()
								">
								{{ SERPpop?.priceFilter[2].min }}-{{ SERPpop?.priceFilter[2].max }}
							</li>
						</ul>
						<ul v-else>
							<li></li>
							<li></li>
							<li></li>
						</ul>
						<div class="chengtab-b">
							<div class="chengtab-b-qj">区间(元)</div>
							<input type="text" v-model="jiage1" />
							<div class="chengtab-b-m"></div>
							<input type="text" v-model="jiage2" />
							<button @click="chengp()">确定</button>
						</div>
					</div>
				</transition>
			</section>
		</div>
		<section class="serp-max">
			<ul v-if="chengg === 1">
				<SERPV v-for="n in SERPpop?.wall?.docs" :key="n.tradeItemId" :n="n" />
			</ul>
			<ul v-if="chengg === 2">
				<SERPV v-for="n in SERPsell?.wall?.docs" :key="n.tradeItemId" :n="n" />
			</ul>
			<ul v-if="chengg === 3">
				<SERPV v-for="n in SERPnew?.wall?.docs" :key="n.tradeItemId" :n="n" />
			</ul>
		</section>
	</div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import SERPV from "@/components/SERPV.vue";

export default {
	name: "SERPView",
	components: {
		SERPV,
	},

	computed: {
		...mapState(["searchvalue", "SERPpop", "SERPsell", "SERPnew"]),
	},
	data() {
		return {
			chengg: 1,
			chengf: false,
			jiage1: null,
			jiage2: null,
		};
	},

	mounted() {},

	methods: {
		...mapMutations([
			"addhistorysearch",
			"addSERPpop",
			"addSERPsell",
			"addSERPnew",
		]),
		back() {
			if (window.history.length <= 1) {
				this.$router.push({ path: "/" });
				return false;
			} else {
				this.$router.go(-1);
			}
		},
		chengp() {
			if (this.jiage1 && this.jiage2) {
				this.getSERP();
				this.jiage1 = null;
				this.jiage2 = null;
				this.chengf = false;
			} else {
				this.jiage1 = null;
				this.jiage2 = null;
				alert("输入格式错误");
			}
		},
		getSERP() {
			if (this.jiage1 && this.jiage2) {
				//综合
				this.axios
					.get("https://apis.netstart.cn/mogujie/search/searchByKeyword", {
						params: {
							q: this.$route.query.q,
							page: 1,
							sort: "pop",
							minPrice: this.jiage1,
							maxPrice: this.jiage2,
							offset: 30,
						},
					})
					.then((ren) => {
						this.addSERPpop(ren?.data?.result);
					});
				//销量
				this.axios
					.get("https://apis.netstart.cn/mogujie/search/searchByKeyword", {
						params: {
							q: this.$route.query.q,
							page: 1,
							sort: "sell",
							minPrice: this.jiage1,
							maxPrice: this.jiage2,
							offset: 30,
						},
					})
					.then((ren) => {
						this.addSERPsell(ren?.data?.result);
					});
				//新品
				this.axios
					.get("https://apis.netstart.cn/mogujie/search/searchByKeyword", {
						params: {
							q: this.$route.query.q,
							page: 1,
							sort: "new",
							minPrice: this.jiage1,
							maxPrice: this.jiage2,
							offset: 30,
						},
					})
					.then((ren) => {
						this.addSERPnew(ren?.data?.result);
					});
			}
		},
	},

	created() {
		//综合
		this.axios
			.get("https://apis.netstart.cn/mogujie/search/searchByKeyword", {
				params: {
					q: this.$route.query.q,
					page: 1,
					sort: "pop",
					offset: 30,
				},
			})
			.then((ren) => {
				this.addSERPpop(ren?.data?.result);
			});
		//销量
		this.axios
			.get("https://apis.netstart.cn/mogujie/search/searchByKeyword", {
				params: {
					q: this.$route.query.q,
					page: 1,
					sort: "sell",
					offset: 30,
				},
			})
			.then((ren) => {
				this.addSERPsell(ren?.data?.result);
			});
		//新品
		this.axios
			.get("https://apis.netstart.cn/mogujie/search/searchByKeyword", {
				params: {
					q: this.$route.query.q,
					page: 1,
					sort: "new",
					offset: 30,
				},
			})
			.then((ren) => {
				this.addSERPnew(ren?.data?.result);
			});
	},
};
</script>

<style lang="less" scoped>
.SERPView {
	.box {
		position: sticky;
		top: 0;
		left: 0;
		.chengnuv {
			display: flex;
			justify-content: space-between;
			align-items: center;
			border-bottom: 1px solid #e5e5e5;
			background-color: #fff;
			z-index: 2;
			.chengnuv-l {
				width: 52rem;
				height: 44rem;
				display: flex;
				justify-content: center;
				align-items: center;
				.chengnuv-l-m {
					width: 22rem;
					height: 22rem;
					background-image: url(@/assets/jianto2.png);
					background-size: contain;
					background-position: center;
					background-repeat: no-repeat;
				}
			}
			.chengnuv-m {
				display: flex;
				align-items: center;
				flex-grow: 1;
				height: 25rem;
				background-color: #eeeeee;
				img {
					width: 25rem;
					height: 25rem;
					transform: scale(0.6);
				}
				input {
					width: 100%;
					font-size: 18rem;
					color: #000;
					height: 24rem;
					border: 0px solid red;
					background-color: #eee;
				}
			}
			.chengnuv-r {
				width: 50rem;
				height: 45rem;
				display: flex;
				justify-content: center;
				align-items: center;
				.chengnuv-r-m {
					width: 22rem;
					height: 22rem;
					background-image: url(@/assets/gwc.png);
					background-size: contain;
					background-position: center;
					background-repeat: no-repeat;
				}
			}
		}
		.serp-tab {
			display: flex;
			align-items: center;
			height: 40rem;
			border-bottom: 1px solid #eaeaea;
			background-color: #fff;
			text-align: center;
			div {
				width: 25%;
				height: 15rem;
				line-height: 15rem;
				font-size: 14rem;
				font-weight: 400;
				border-right: 1px solid #eaeaea;
			}
			div.cheng {
				color: #ef4562;
			}
			.chengtab {
				position: absolute;
				top: 84rem;
				left: 0;
				width: 375rem;
				height: 100rem;
				background-color: #fff;
				ul {
					width: 375rem;
					margin: 7.5rem 0 3.75rem 0;
					display: flex;
					flex-wrap: wrap;
					li {
						display: flex;
						justify-content: center;
						align-items: center;
						width: 112.55rem;
						height: 30rem;
						margin: 7.5rem 0 0 7.5rem;
						background-color: #f3f3f3;
					}
				}
				.chengtab-b {
					width: 100%;
					height: 50rem;
					display: flex;
					align-items: center;
					padding: 0 7.5rem;
					.chengtab-b-qj {
						display: flex;
						justify-content: center;
						align-items: center;
						width: 51.5rem;
						height: 50rem;
					}
					input {
						width: 73rem;
						height: 28rem;
						padding: 0 5rem;
						border: 1px solid #e5e5e5;
						margin-left: 10rem;
						box-sizing: content-box;
					}
					.chengtab-b-m {
						margin-left: 10rem;
						width: 10rem;
						height: 0;
						border-top: 1px solid #cbcbcb;
					}
					button {
						margin-left: 5rem;
						width: 85rem;
						height: 30rem;
						padding: 0;
						border: 0 solid #cbcbcb;
						background-color: #ff5b76;
						color: #fff;
					}
				}
			}
		}
	}
	.serp-max {
		background-color: #efefef;
		ul {
			display: flex;
			flex-wrap: wrap;
		}
	}
}
</style>
