			console.log("播放直播");
			if (flvjs.isSupported()) {
				var videoElement = this.$refs.video;
				var flvPlayer = flvjs.createPlayer({
					type: "flv",
					url: this.liveUrl,
				});
				flvPlayer.attachMediaElement(videoElement);
				flvPlayer.load();
				// flvPlayer.play();
			}
		},
		pla() {
			console.log("开始播放直播");
			this.show = false;
			this.$refs.video.play();
			let s = 0;
			this.a = setInterval(() => {
				s += 30;
				this.$refs.dms.style.transform = `translateY(-${s}rem)`;
			}, 3000);
		},
		back() {
			if (window.history.length <= 1) {
				this.$router.push({ path: "/" });
				return false;
			} else {
				this.$router.go(-1);
			}
		},
	},
	created() {
		this.axios
			.get("https://apis.netstart.cn/mogujie/live/actorLiveInfo", {
				params: {
					roomId: this.roomId,
					actorId: this.actUserId,
				},
			})
			.then((ren) => {
				this.liveUrl = ren?.data?.data.liveUrl;
				this.dbrall = ren?.data?.data;
				this.getsp();
			});
	},
	destroyed() {
		clearInterval(this.a)
	},
};
